* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
li {
  list-style: none;
  padding: 0;
}

li {
  display: flex;
  align-items: center;
}
img,
h2 {
  display: inline;
}

img {
  margin-right: 15px;
}

.country_name {
  font-size: 36px;
}

p {
  font-size: 24px;
  font-weight: bold;
}

span {
  font-weight: normal;
}
